@import url(https://fonts.googleapis.com/css?family=Lato:100&display=swap);
body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ededed86
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.content {
  display: flex;
  position: relative;
  z-index: 25;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.headText {
color: #dc9852
}
.backgroundImage {
  z-index: 10;
  opacity: 50%;
  height: 500px;
  width: 800px;
  background: url(../../static/media/fox.dc8a82bf.png) no-repeat center center fixed;
}
/* add css module styles here (optional) */

._styles-module__test__3ybTi {
  margin: 2em;
  padding: 0.5em;
  border: 2px solid #000;
  font-size: 2em;
  text-align: center;
}
svg {
  position: absolute;
  left: 0;
  top: 0;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}

._styles-module__primary__3ljpl {
  width: 180px;
  height: 60px;
  background: transparent;
  border: 1px solid #efb560;
  outline: none;
  transition: 1s ease-in-out;
}
._styles-module__primary__3ljpl:hover {
  transition: 1s ease-in-out;
  background: #efb560;
  color: #fff;
}
._styles-module__primary__3ljpl:hover svg {
  stroke-dashoffset: -480;
}

